import {useState, useEffect} from 'react';
import {useLocation} from 'react-router-dom';
import '../css/productDetail.css';
import uniqid from 'uniqid';
import truckIcon from '../imgs/truck.png';
import packageIcon from '../imgs/package-box.png';
import giftIcon from '../imgs/giftbox.png';
import Loading from './loading';

function ProductDetail(props) {
    const location = useLocation();
    const [product, setProduct] = useState({});
    const [loaded, setLoaded] = useState(false);

    useEffect(() => {
        const url = `https://e-commerce-back.onrender.com/collection/${location.state.productId}`;

        fetch(url)
        .then(data=>data.json())
        .then(response=>{
            if (response.status !== 200) {
                return;
            }
            setProduct(response.collection.collection);
            setLoaded(true);
        })
    }, [])

    return (
        <>{(loaded)?<div className="detail-div">
        <div className="detail-content">
            <div className="detail-img-container">
                <img src={product.fotoUrl} alt={product.name} className="product-img"/>
            </div>
            <div className="detail-description">
                <div className="detail-item-name-price bold">
                    <p>{product.name}</p>
                    <p className="text-end">{product.price}$</p>
                </div>
                <div className="detail-item">
                    <div className="color-name">
                        <p className="bold">Color </p> 
                        <small>
                            <div className="color-name-ul">
                                {(product.color !== undefined)?product.color.map(c => {
                                    return <span key={uniqid()}>{c.name.toUpperCase()}</span>
                                }):null}   
                            </div>
                        </small>
                    </div> 
                    <ul className="product-color-ul">
                        {(product.color !== undefined)?product.color.map(c => {
                            return <li key={uniqid()} className="product-color" style={{backgroundColor: c.rgbValue}}></li>
                        }):null}
                    </ul>
                </div>
                <div className="detail-item">
                    <div className="color-name">
                        <div className="bold">Size</div> 
                        <div><small>SIZE GUIDE</small></div>
                    </div>
                    <ul className="product-size-ul">
                        {(product.size !== undefined)?product.size.map(c => {
                            return <li key={uniqid()} className="product-size"><span>{c.name}</span></li>
                        }):null}
                    </ul>
                </div>
                <div className="button-container">
                    <button className="add-to-bag-btn"> ADD TO BAG </button>
                </div>
                <div className="hr-line"></div>
                <div className="detail-item product-description bold">
                    <p>{product.description}</p>
                </div>
                <div className="detail-item">
                    <div className="color-name">
                        <div className="bold">Materials</div> 
                        <small>
                            <div className="color-name-ul">
                                {(product.material !== undefined)?product.material.map(c => {
                                    return <span key={uniqid()}>{c.name.toUpperCase()}</span>
                                }):null}   
                            </div> 
                        </small>
                    </div>
                </div>
                <div className="detail-item">
                    <div className="color-name">
                        <div className="bold">Fit</div> 
                        <small>
                            <div className="color-name-ul">
                                {(product.fit !== undefined)?product.fit.map(c => {
                                    return <span key={uniqid()}>{c.name.toUpperCase()}</span>
                                }):null}   
                            </div> 
                        </small>
                    </div>
                </div>
                <div className="detail-item">
                    <div className="color-name">
                        <div className="bold">Care</div> 
                        <small>
                            <div className="color-name-ul">
                                {(product.care !== undefined)?product.care.map(c => {
                                    return <span key={uniqid()}>{c.name.toUpperCase()}</span>
                                }):null}   
                            </div> 
                        </small>
                    </div>
                </div>
                <div className="hr-line"></div>
                <div className="policy-container text-center">
                    <div className="policy-item">
                        <div className="policy-icon-container">
                            <div className="policy-icon" style={{backgroundImage: `url(${truckIcon})`}}></div>
                        </div>
                        <p className="bold">Free shipping</p>
                        <p>For orders over 130$.</p>
                        <small><p className="underline">LEARN MORE</p></small>
                    </div>
                    <div className="policy-item">
                        <div className="policy-icon-container">
                            <div className="policy-icon" style={{backgroundImage: `url(${packageIcon})`}}></div>
                        </div>
                        <p className="bold">Extended returns</p>
                        <p>Return within 60 days of shipping for a fee of $8 duties and taxes refundable upon return of the product.</p>
                        <small><p className="underline">LEARN MORE</p></small>
                    </div>                    
                    <div className="policy-item">
                        <div className="policy-icon-container">
                            <div className="policy-icon" style={{backgroundImage: `url(${giftIcon})`}}></div>
                        </div>
                        <p className="bold">Send it as a gift</p>
                        <p>Add a personalized note during checkpoint.</p>
                        <small><p className="underline">LEARN MORE</p></small>
                    </div>
                </div>
            </div>
        </div>
    </div>
    :<div className="loading-div">
        <Loading />
    </div>}</>
    )
}

export default ProductDetail;